.container__slider {
    position: relative;
    width: 100%;
    padding: 0;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
}

.container__slider > button {
    background-color: transparent;
    outline: none;
    border: 0;
}

.slider__btn-next {
    position: absolute;
    right: 0;
}

.slider__btn-prev {
    position: absolute;
    left: 0;
}

.container__slider__links {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
}

.container__slider__links-small {
    position: relative;
    width: 8px;
    height: 8px;
    padding: 0;
    border-radius: 50%;
    outline: none;
    background-color: white;
    border: 1px solid grey;
    transition: background-color 1s ease;
    margin: 0 2px;
}

.container__slider__links-small-active {
    background-color: black;
}

.slider__item {
    box-sizing: border-box;
    min-width: 100%;
    height: 100%;
    overflow: hidden;
    object-fit: cover;
    transform: translateX(0);
    transition: transform 1s ease;
}

.slider__item-active-1 {
    transform: translateX(0);
}

.slider__item-active-2 {
    transform: translateX(-100%);
}

.slider__item-active-3 {
    transform: translateX(-200%);
}

.slider__item-active-4 {
    transform: translateX(-300%);
}

.slider__item-active-5 {
    transform: translateX(-300%);
}

.slider__item-active-6 {
    transform: translateX(-300%);
}

.slider__item-active-7 {
    transform: translateX(-300%);
}

.slider__item img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

@media (max-width: 800px) {
    .slider__btn-prev img{
        width: 20px;
        height: 20px;
    }
    .slider__btn-next img{
        width: 20px;
        height: 20px;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .slider__btn-prev img{
        width: 25px;
        height: 25px;
    }
    .slider__btn-next img{
        width: 25px;
        height: 25px;
    }
}